import { NavLink } from "react-router-dom";

import logo from "assets/images/logo.png";

import "./Default.css";

const LINKS = [
  {
    to: "/",
    label: "Home",
  },
  {
    to: "/governance",
    label: "Governance",
  },
  {
    to: "/opportunities",
    label: "Opportunities",
  },
  {
    to: "/events",
    label: "Events",
  },
  {
    to: "/support",
    label: "Support",
  },
];

const NavigationDefault = () => {
  return (
    <div className="bg-color-maroon px-5 pb-5">
      <nav className="navbar navbar-dark navbar-expand-lg py-0 mx-5">
        <div className="container-fluid bg-color-gold mx-5 navbar-rounded">
          <img src={logo} className="navbar-brand-img mt-5 mx-5" alt="" />
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse justify-content-center" id="navbarNav">
            <ul className="navbar-nav navbar-dark py-2 px-5">
              {LINKS.map(({ to, label }) => (
                <li className="nav-item mx-3" key={to}>
                  <NavLink
                    to={to}
                    exact
                    activeClassName="active"
                    className="nav-link text-uppercase text-white fw-bold"
                  >
                    {label}
                  </NavLink>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </nav>
      <div className="mx-5 px-5">
        <div className="mx-5 px-5">
          <div className="navbar-brand bg-color-primary mx-3 p-0 px-5 navbar-rounded">
            IIT Madras Online Degree Students
          </div>
        </div>
      </div>
    </div>
  );
};

export default NavigationDefault;